// Copied from Cppkies 2 : Electric Boogaloo

import {
    CrateLike,
    AdvancedAchievement,
    AdvancedHeavenlyUpgrade,
    AdvancedUpgrade,
    AuthorAchievement,
    TagCondition,
    TagConditional,
    TagConditions
} from "./core"

import {
    PrestigePage,
    RiftUpgrade,
    PagedHeavenlyUpgrade,
} from "./rifts"

const SkullyCore = {
    // Base
    AdvancedAchievement,
    AdvancedHeavenlyUpgrade,
    AdvancedUpgrade,
    AuthorAchievement,
    TagCondition,
    TagConditions,

    // Prestige Pages & Rifts
    PrestigePage,
    RiftUpgrade,
    PagedHeavenlyUpgrade,

    // OnLoad
    onLoad: [] as (() => void)[],
    currentPage: "vanilla",
    pages: {"vanilla": Game.PrestigeUpgrades} as {[pageid: string]: Game.HeavenlyUpgrade[]}
}

// Stolen from Cppkies
declare global {
	interface Window {
		SkullyCore: typeof SkullyCore | undefined
	}
}
window.SkullyCore = SkullyCore
window.SkullyCore.TagConditions = SkullyCore.TagConditions;

export default SkullyCore
export { 
    CrateLike, 
    TagConditional 
}
